const WILDCARD = '*'

/* event-emitter */
class EventEmitter {
  constructor() {
    this._events = {}
  }

  /**
   * 
   * @param evt
   * @param callback
   * @param once
   */
  on(evt, callback, once) {
    const evts = evt.split(',')
    evts.forEach((ev) => {
      if (!this._events[ev]) {
        this._events[ev] = []
      }
      this._events[ev].push({
        callback,
        once: !!once
      })
    })

    return this
  }

  /**
   * 
   * @param evt
   * @param callback
   */
  once(evt, callback) {
    return this.on(evt, callback, true)
  }

  /**
   * 
   * @param evt
   * @param args
   */
  emit(evt, eventArgs) {
    const events = this._events[evt] || []
    const wildcardEvents = this._events[WILDCARD] || []

    //  emit 
    const doEmit = (es) => {
      let { length } = es
      for (let i = 0; i < length; i++) {
        if (!es[i]) {
          // eslint-disable-next-line no-continue
          continue
        }
        const { callback, once } = es[i]

        if (once) {
          es.splice(i, 1)

          if (es.length === 0) {
            delete this._events[evt]
          }

          length--
          i--
        }

        callback.apply(this, [eventArgs])
      }
    }

    doEmit(events)
    doEmit(wildcardEvents)
  }

  /**
   * channel
   * @param evt
   * @param callback
   */
  off(evt, callback) {
    if (!evt) {
      // evt 
      this._events = {}
    } else if (!callback) {
      // evt callback 
      delete this._events[evt]
    } else {
      // evt callback 
      const events = this._events[evt] || []

      let { length } = events
      for (let i = 0; i < length; i++) {
        if (events[i].callback === callback) {
          events.splice(i, 1)
          length--
          i--
        }
      }

      if (events.length === 0) {
        delete this._events[evt]
      }
    }

    return this
  }

  /*  */
  getEvents() {
    return this._events
  }
}

export { EventEmitter }
