import * as esprima from 'esprima'
import * as acornWalk from 'acorn-walk'

const blacklist = new Set(['eval', 'setTimeout', 'setInterval', 'fetch', 'Image', 'XMLHttpRequest', 'ActiveXObject'])

function hasBlacklist(node) {
  if (node.type === 'Identifier') {
    if (blacklist.has(node.name)) {
      return true
    }
  }
  return false
}

export function validateCode(code) {
  const ast = esprima.parse(code)
  console.log('ast --->>>', ast)

  let hasWindow = false
  let hasDocument = false
  let hasReturn = false

  acornWalk.simple(ast, {
    MemberExpression(node) {
      if (node.object.name === 'window') {
        hasWindow = true
      } else if (node.object.name === 'document') {
        hasDocument = true
      }
    },

    CallExpression(node) {
      if (hasBlacklist(node.callee)) {
        throw new Error(` API ${node.callee.name}`)
      }
    },

    ReturnStatement(node) {
      hasReturn = true
    }
  })

  if (hasWindow || hasDocument) {
    throw new Error(' window  document ')
  }

  if (!hasReturn) {
    throw new Error(' return ')
  }
  return hasReturn
}
